textarea:hover,
input:hover,
textarea:active,
input:active,
textarea:focus,
input:focus,
button:focus,
button:active,
button:hover,
label:focus,
.btn:active,
.btn.active {
  outline: 0px !important;
  -webkit-appearance: none;
  box-shadow: none !important;
}


body {
  font-family: 'Ubuntu', sans-serif;
}

.search {
  max-width: 25%;
}

.track {
  display: inline-block;
  width: 250px;
  height: 250px;
  cursor: pointer;
  position: relative;
  margin: 10px 10px;
}

.track img {
  width: 250px;
  height: 250px;
}

.track-text {
  position: absolute;
  text-align: center;
  width: 100%;
  bottom: 0;
  color: white;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.178), rgba(0, 0, 0, -0.812));
  padding: 10px;

}

#play-icon {
  position: absolute;
  left: 45%;
  bottom: 45%;
  background-color: rgba(0, 0, 0, 0.562);
  padding: 10px 15px;
  color: #fff;
  opacity: 0.8;
  text-shadow: -1px 4px 5px rgba(0, 0, 0, 0.81);

}

@media (max-width: 768px) {
  .search {
    max-width: 80%;
  }


  .track {
    width: 90%;
    height: 90%;
    margin: 10px auto;
  }

  .track img {
    width: 100%;
    height: 100%;
  }

  .track-text {

    bottom: 3px;


  }
}